import React from 'react';
import {
  TextInput,
  MultipleCheckboxes,
  MultipleCheckboxesLimitedHeight,
  LongTextInput,
  UploadFileInput,
  YesOrNo,
} from './FormField';
import { VN_CITIES } from './data';

export const FormQuestions = [
  {
    element: (props) => (
      <UploadFileInput
        title="nh cho bi ng"
        subtitle={<>
          nh ny s c dng  ng ln fanpage, thng s l avatar ca d n.<br/>
          Lu :<br/>
          - Chng mnh KHNG CHP NHN nh chp tp th ca BTC<br/>
          - Khng chp nhn cc nh d gy hiu nhm, hoc c ni dung khng lnh mnh<br/>
          - nh nn c t l vung (1:1)  hin tt trn c instagram v facebook<br/>
        </>}
        required={true}
        {...props}
      />
    ),
    name: 'image',
    defaultValue: () => null,
    checkError: (value) => {
      if (!value) return 'Bn cha chn nh  ng ln page chng mnh';
    },
    populateToDoc: (value, doc) => {
      doc['image'] = [value];
    },
  },

  {
    element: (props) => (
      <TextInput
        title="Tn d n/cuc thi/s kin"
        subtitle="Hy nhp 1 tn ngn gn cho d n, cuc thi hay s kin ca bn nh"
        placeholder="V d: ABC Project..."
        required={true}
        {...props}
      />
    ),
    name: 'name',
    defaultValue: () => '',
    checkError: (value) => {
      if (!value.trim()) return 'Bn cha in tn d n/cuc thi/s kin';
    },
    populateToDoc: (value, doc) => {
      doc['name'] = value.trim();
    },
  },

  {
    element: (props) => (
      <MultipleCheckboxes
        title="Phn loi"
        subtitle="Bn c th thm phn mc d n ca bn (nu khng c trong danh sch ny) di dng hashtag"
        moreLabel="Hoc nhp thm cc hashtag khc..."
        placeholder="V d: #d_n_ABC"
        required={true}
        {...props}
      />
    ),
    name: 'type',
    defaultValue: () => ({
      '#d_n_gio_dc': false,
      '#d_n_tnh_nguyn': false,
      '#d_n_vn_ha': false,
      '#d_n_ngh_thut': false,
      '#d_n_khoa_hc': false,
      '#d_n_mi_trng': false,
      '#cuc_thi': false,
      '#s_kin': false,
      'other': '',
    }),
    checkError: (value) => {
      const atLeastOneFilled = Object.values(value).some(t => !!t);
      const hasOther = !!value.other.trim();
      const otherHasHash = !!value.other.match(/#/);
      if (!atLeastOneFilled) return 'Bn cha chn phn ny';
      if (hasOther && !otherHasHash) return 'Phn  in phi c du hashtag, v d #d_n_ABC';
    },
    populateToDoc: (value, doc) => {
      const arr = Object.entries(value)
        .filter(([key, value]) => key !== 'other' && value)
        .map(a => a[0]);
      const other = value.other.trim();
      if (!!other) {
        arr.push(other);
      }
      doc['type'] = arr;
    },
  },

  {
    element: (props) => (
      <LongTextInput
        title="M t v d n/cuc thi/s kin"
        subtitle={<>
          Lu :<br/>
          - Hy vit tht chi tit,  ngi c c th hiu d n/cuc thi/s kin ca bn hng n iu g nh.<br/>
          - KHNG copy-paste link bi h tr truyn thng (phi vit y  ni dung vo y)<br/>
          - Cc thng tin v nh ti tr s b xa sau khi kim duyt.<br/>
          - Bn cng c th  link n ng k (nu c) trong phn m t<br/>
        </>}
        placeholder="V d: ABC Project l d n c lp ra bi... Chng mnh hng ti vic..."
        required={true}
        rows={15}
        {...props}
      />
    ),
    name: 'description',
    defaultValue: () => '',
    checkError: (value) => {
      const v = value.trim();
      if (!v) return 'Bn cha in phn m t ny';
      if (v.length < 400) return 'Phn m t cho d n qu ngn. Bn hy vit 1 cch chi tit hn (d n ca bn lm g, c  ngha th no,...). Hy vit chi tit, KHNG copy-paste link bi vit';
      // eslint-disable-next-line
      const urls = v.match(/https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([^ \n\r\t]+)/gi);
      if (urls) {
        for (const url of urls) {
          if (url.match(/\.\.\./)) return `Do phn m t bn copy t facebook, nn link sau: "${url}"  b facebook rt ngn (c du 3 chm). V vy, c gi s khng th bm vo c. Bn nn t li link gc, hoc dng bit.ly hay bom.to  rt gn link nh!`;
        }
      }
    },
    populateToDoc: (value, doc) => {
      doc['description'] = value.trim();
    },
  },

  {
    element: (props) => (
      <MultipleCheckboxesLimitedHeight
        title="Tnh, thnh ph"
        subtitle="Ni d n / cuc thi / s kin ca bn s din ra. Bn c th chn nhiu tnh, thnh ph (hy cun sang ngang  tm)"
        required={true}
        {...props}
      />
    ),
    name: 'locations',
    defaultValue: () => {
      const val = {};
      for (const c of VN_CITIES) val[c] = false;
      return val;
    },
    checkError: (value) => {
      const atLeastOneFilled = Object.values(value).some(t => !!t);
      if (!atLeastOneFilled) return 'Bn cha chn phn ny';
    },
    populateToDoc: (value, doc) => {
      const arr = Object.entries(value)
        .filter(a => a[1])
        .map(a => a[0]);
      doc['locations'] = arr;
    },
  },

  {
    element: (props) => (
      <TextInput
        title="Link facebook page"
        subtitle="Hy nhp link ti trang facebook chnh ca d n, cuc thi hay s kin ca bn nh"
        placeholder="V d: https://www.facebook.com/tonghopevent"
        required={true}
        {...props}
      />
    ),
    name: 'linkfb',
    defaultValue: () => '',
    checkError: (value) => {
      const v = value.trim();
      if (!v) return 'Bn cha in mc ny';
      if (!v.match(/(fb|facebook)\.com/)) return 'Link bn nhp khng ng nh dng. V d, link ti facebook ca chng mnh phi l: https://www.facebook.com/tonghopevent';
    },
    populateToDoc: (value, doc) => {
      doc['linkfb'] = value;
    },
  },

  {
    element: (props) => (
      <YesOrNo
        title="D n ca bn c tuyn nhn s khng?"
        subtitle="V d nh tuyn TNV, tuyn Ban iu hnh, ngi tham gia,..."
        required={true}
        {...props}
      />
    ),
    name: '_recruitment',
    defaultValue: () => null,
    checkError: (value) => {
      if (value === null) return 'Bn cha chn mc ny';
    },
    populateToDoc: (value, doc) => null,
  },

  {
    dependsOn: '_recruitment',
    element: (props) => (
      <MultipleCheckboxes
        title="Yu cu i tng"
        moreLabel="Hoc cc i tng khc... (khng bt buc in)"
        placeholder="Khc..."
        {...props}
      />
    ),
    name: 'wanted',
    defaultValue: () => ({
      'Ban iu hnh': false,
      'Tnh nguyn vin': false,
      'Ngi tham gia': false,
      'other': '',
    }),
    checkError: (value) => null,
    populateToDoc: (value, doc) => {
      const arr = Object.entries(value)
        .filter(([key, value]) => key !== 'other' && value)
        .map(a => a[0]);
      if (value.other) arr.push(value.other);
      doc['wanted'] = arr;
    },
  },

  {
    dependsOn: '_recruitment',
    element: (props) => (
      <LongTextInput
        title="Deadline tuyn nhn s"
        subtitle="Nu bn c deadline ring cho tng ban, bn cng c th ghi r trong phn ny nh"
        placeholder="..."
        rows={2}
        {...props}
      />
    ),
    name: 'deadline',
    defaultValue: () => '',
    checkError: (value) => null,
    populateToDoc: (value, doc) => doc['deadline'] = value.trim(),
  },

  {
    dependsOn: '_recruitment',
    element: (props) => (
      <LongTextInput
        title="Quyn li thnh vin khi tham gia d n"
        subtitle="V d: nh c chng ch, c giao lu, hc hi kinh nghim v lnh vc g,..."
        placeholder="VD: Tham gia ABC Project, bn s nhn c... v ngoi ra..."
        rows={6}
        {...props}
      />
    ),
    name: 'benefit',
    defaultValue: () => '',
    checkError: (value) => null,
    populateToDoc: (value, doc) => doc['benefit'] = value.trim(),
  },

  // Thng tin thm

  {
    element: (props) => (
      <TextInput
        title="Bn c lu , cu hi g cho chng mnh khng?"
        subtitle="(Khng bt buc) V d: mun ng bi vo ngy no,..."
        placeholder="..."
        {...props}
      />
    ),
    name: 'feedback',
    defaultValue: () => '',
    checkError: (value) => null,
    populateToDoc: (value, doc) => doc['feedback'] = value.trim(),
  },

];